import { Component } from 'react';
import traitify from "traitify-widgets"


class Assessment extends Component {

    state = { created_assessment: {} }

    componentDidMount() {
        // Simple POST request with a JSON body using fetch
        const requestOptions = {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': 'Basic a107c7cbe71b43f68c9bb41f24d9425f:x'
            },
            body: JSON.stringify({
                'deck_id': 'big-five'
            })

        };
        fetch('https://api.traitify.com/v1/assessments', requestOptions)
            .then(response => response.json())
            .then(data => this.setState({ created_assessment: data }));
    }

    render() {

        sessionStorage.setItem("assessment_id", this.state.created_assessment.id);

        traitify.setPublicKey("1bca69eb32444d58948cd5fc0ef1cf81");
        const assessment = traitify.ui.component();
        assessment.assessmentID(`${this.state.created_assessment.id}`);
        assessment.target("#assessment");
        assessment.render("SlideDeck");

        return
    }
}

export default Assessment;